$red: hsl(0, 78%, 62%);
$cyan: hsl(180, 62%, 55%);
$orange: hsl(34, 97%, 64%);
$blue: hsl(212, 86%, 64%);
$veryDarkBlue: hsl(234, 12%, 34%);
$grayishBlue: hsl(229, 6%, 66%);
$veryLightGray: hsl(0, 0%, 98%);

body{
    font-family: 'Poppins', sans-serif;
    text-align: center;
    margin: 83px 32px 83px 32px;
    background-color: white;
}

p{
    font-family: 'Poppins', sans-serif;
    font-size: 15px;
}

#tagline-top{
    font-size: 24.3px;
    font-weight: 200;
    color: $veryDarkBlue;
    margin-bottom: 0px;
}

#tagline-bottom{
    font-size: 24.3px;
    font-weight: 600;
    color: $veryDarkBlue;
    margin-top: 1px;
    margin-bottom: 0px;
}

#intro{
    margin-top: 16px;
    color: $grayishBlue;
    font-weight: 400;
    line-height: 24px;
    margin-bottom: 79px;
}

.product-section{
    background-color: gray; //temporary
    width: 100%;
    height: 222px;
    border-radius: 5px;
    border-top: 3px solid;
    text-align: left;

    h1{
        color: $veryDarkBlue;
        font-size: 21px;
        margin: 21px auto auto 27px;
    }
    
    p{
        margin: 4px auto auto 27px;
        font-size: 13.2px;
        line-height: 22px;
    }

}

#supervisor-section{
    border-top-color: $cyan;
}